import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StartPageComponent } from './start-page/start-page.component';
import { ProfileComponent } from './profile/profile.component';
import { SignupComponent } from './signup/signup.component';
import { SearchProfileComponent } from './search-profile/search-profile.component';
import { EducationEditComponent } from './education-edit/education-edit.component';
import { ExperienceEditComponent } from './experience-edit/experience-edit.component';
import { AuthGuard } from './auth.guard';

const routes: Routes = [
  {path: 'signup', component: SignupComponent}, 
  {path: 'profile/:uname', component: ProfileComponent},
  {path: 'searchprofile/:name', component: SearchProfileComponent},
  {path: 'EditBtech/:username', component: EducationEditComponent},
  {path: 'EditExperience/:username', component: ExperienceEditComponent},
  { path: '',  component: StartPageComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { 
  
}
